.proposal__wrapper {
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding: 0 20px;

  @include container;
}

.proposal__container {
  background-color: $second-background-color;
  padding: 30px 20px 18px 20px;
}

.proposal__title {
  position: relative;
  margin: 0 0 29px 71px;
  font-family: $main-font;
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
  color: $main-color;

  &--skinny {
    &::before {
      position: absolute;
      top: 50%;
      left: -19px;
      content: "";
      width: 50px;
      height: 53px;
      background: url("../img/index/cat-skinny.svg") no-repeat center / contain;
      transform: translateY(-50%) translateX(-100%);
    }
  }

  &--muscular {
    &::before {
      position: absolute;
      top: 50%;
      left: -11px;
      content: "";
      width: 67px;
      height: 50px;
      background: url("../img/index/cat-muscular.svg") no-repeat center /
        contain;
      transform: translateY(-50%) translateX(-100%);
    }
  }
}

.proposal__description {
  @include main-paragraph;
  margin-bottom: 38px;
}

.proposal__link {
  position: relative;
  display: inline-block;
  padding-right: 39px;
  font-family: $main-font;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  color: $main-color;
  text-decoration: none;

  &::after {
    content: "";
    position: absolute;
    top: 50%;
    right: 0;
    display: block;
    background: url("../img/index/arrow/arrow.svg") no-repeat center;
    width: 24px;
    height: 12px;
    transform: translateY(-50%);
  }

  &:hover {
    padding-right: 47px;

    &::after {
      width: 32px;
      background: url("../img/index/arrow/long-arrow.svg") no-repeat center;
    }
  }

  &:active {
    opacity: 0.3;
  }
}

@media (min-width: $tablet-width) {
  .proposal__wrapper {
    gap: 30px;
    padding: 0 30px 0 31px;
  }

  .proposal__container {
    padding: 77px 44.63% 53px 7.34%;
  }

  .proposal__title {
    font-size: 36px;
    line-height: 36px;
    margin: 0 0 34px 0;

    &--skinny {
      &::before {
        top: 0;
        left: 393px;
        width: 200px;
        height: 211px;
        transform: translateY(0) translateX(0);
      }
    }

    &--muscular {
      &::before {
        top: 0;
        left: 359px;
        width: 268px;
        height: 200px;
        transform: translateY(0) translateX(0);
      }
    }
  }

  .proposal__description {
    margin-bottom: 48px;
    font-size: 16px;
    line-height: 24px;
  }

  .proposal__link {
    padding-right: 49px;

    &:hover {
      padding-right: 57px;
    }
  }
}

@media (min-width: $desktop-width) {
  .proposal__wrapper {
    flex-direction: row;
    padding: 0 110px;
  }

  .proposal__container {
    padding: 77px 52px 55px 52px;
  }

  .proposal__title {
    margin-bottom: 73px;
    margin-left: 162px;

    &--skinny {
      &::before {
        top: 50%;
        left: -62px;
        width: 100px;
        height: 106px;
        transform: translateY(-50%) translateX(-100%);
      }
    }

    &--muscular {
      &::before {
        top: 50%;
        left: -47px;
        width: 134px;
        height: 100px;
        transform: translateY(-50%) translateX(-100%);
      }
    }
  }

  .proposal__description {
    margin-bottom: 23px;
  }
}
